import { Link } from "react-router-dom"
import { useAccount } from "@rp/react/common/providers"
import { WatchlistsOutlined } from "@rp/react/web/icons"
import { Box, Typography, alpha, styled } from "@rp/react/web/mui/core"
import { ChatBubbleOutline, DynamicFeed, FolderOpen } from "@rp/react/web/mui/icons"
import { CHATS_ROUTE, FILES_ROUTE, SEARCHES_ROUTE, WATCHLISTS_ROUTE } from "@rp/react/web/router"
import { DrawerBase } from "../DrawerBase"

type Props = { open: boolean; onClose: () => void; onChange: (newPath: string) => void }

const StyledLink = styled(Link)(({ theme }) => ({
  border: "1px solid rgba(255, 255, 255, 0.12)",
  borderRadius: theme.spacing(0.5),
  width: "100%",
  display: "flex",
  padding: theme.spacing(2),
  fontSize: 14,
  alignItems: "center",
  gap: theme.spacing(2),
  textDecoration: "none",
  "& p": {
    textTransform: "none",
    color: theme.palette.text.primary,
  },
  "& svg": {
    width: 24,
    height: 24,
    color: theme.palette.primary.main,
  },
  "& em": {
    borderRadius: "4px",
    padding: "4px 8px 0px 8px",
    backgroundColor: alpha(theme.palette.primary.main, 0.2),
  },
}))

function DrawerMyContent({ open, onClose, onChange }: Props) {
  const { hasEntitlement } = useAccount()

  return (
    <DrawerBase open={open} toggleDrawer={onClose}>
      <Box sx={{ padding: "0 16px", display: "flex", flexDirection: "column", gap: 2 }}>
        {hasEntitlement("chat") ? (
          <StyledLink to={CHATS_ROUTE} onClick={() => onChange(CHATS_ROUTE)}>
            <Box component="em">
              <ChatBubbleOutline />
            </Box>
            <Typography>Chats</Typography>
          </StyledLink>
        ) : null}
        {hasEntitlement("file") ? (
          <StyledLink to={FILES_ROUTE} onClick={() => onChange(FILES_ROUTE)}>
            <Box component="em">
              <FolderOpen />
            </Box>
            <Typography>Files</Typography>
          </StyledLink>
        ) : null}
        {hasEntitlement("search") ? (
          <StyledLink to={SEARCHES_ROUTE} onClick={() => onChange(SEARCHES_ROUTE)}>
            <Box component="em">
              <DynamicFeed />
            </Box>
            <Typography>Searches</Typography>
          </StyledLink>
        ) : null}
        {hasEntitlement("watchlist") ? (
          <StyledLink to={WATCHLISTS_ROUTE} onClick={() => onChange(WATCHLISTS_ROUTE)}>
            <Box component="em">
              <WatchlistsOutlined />
            </Box>
            <Typography>Watchlists</Typography>
          </StyledLink>
        ) : null}
      </Box>
    </DrawerBase>
  )
}

export { DrawerMyContent }
